import React from 'react'

const Updating = () => {
	return (
		<>
			<p>Since the platform does not provide a version control system, updating can be difficult. However, we have provided some suggestions here that may help you keep your templates more easily updated.</p>
			<ul>
				<li>Before starting your new project, you might host the original template to your own reposity.</li>
				<li>Pull a branch from the repository as your workspace.</li>
				<li>For every new update released, you can push the latest version content to the repository.</li>
				<li>Merging workspace branch with the original branch to update the latest changed.</li>
			</ul>
		</>
	)
}

export default Updating